* {
  box-sizing: border-box;
}

body, h1, h2, h3, p, ul, li, ol {
  margin: 0;
}

a {
  text-decoration: none;
}

ul, li, ol {
  list-style: none;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__img {
  width: 40px;
  height: 40px;
}

.nav__items {
  flex-direction: row;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__items .item__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__items .item__link:hover, .nav__items .item__link.is-active {
  color: #00acdc;
}

.nav__items .list__item:not(:last-child) {
  margin-right: 20px;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.product {
  cursor: pointer;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  max-width: 200px;
  padding: 32px 16px 16px;
  transition: color .3s, transform .3s;
  display: flex;
  position: relative;
}

.product__img {
  object-fit: contain;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.product__name {
  color: #060b35;
  margin-bottom: 4px;
  font-weight: 500;
}

.product__name, .product__price-title {
  font-size: 12px;
  line-height: 18px;
}

.product__description {
  color: #616070;
  margin-bottom: 16px;
}

.product__info {
  align-items: end;
  margin-bottom: 24px;
}

.product__description, .product__info-review {
  font-size: 10px;
  line-height: 14px;
}

.product__info-review {
  color: #060b35;
}

.product__info, .product__price {
  justify-content: space-between;
  display: flex;
}

.product__info-rate {
  display: flex;
}

.product__price {
  margin-bottom: 16px;
}

.product__price-title {
  color: #616070;
}

.product__price-count {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product:hover {
  z-index: 1;
  transform: scale(1.2);
}

.product:hover .product__name {
  color: #34568b;
}

.cards {
  grid-template-columns: 200px;
  justify-content: center;
  gap: 46px 48px;
  margin: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards {
    grid-template-columns: repeat(4, 200px);
  }
}

.button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: color .3s, background-color .3s;
}

.button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

html {
  font-family: Roboto, sans-serif;
}

